import MaterialIcon from '@material/react-material-icon';
import React from 'react';
import { Link } from 'react-router-dom';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
} from 'reactstrap';

const UploadDocument = () => {
  return (
    <Card className="rounded-4">
      <CardBody>
        <CardTitle tag="h4">Tender Terbatas Konstruksi Barrier Stockpile Sulful</CardTitle>
        <small>Pra Kualifikasi</small>
        <Form className="mt-4">
          <Row>
            <Col sm="12" lg="6">
              <FormGroup>
                <Label htmlFor="file1">Surat Penyampaian Penawaran</Label>
                <Input type="file" id="file1" name="file1" />
                <div className="d-flex justify-content-between">
                  <small className="text-muted">Upload file (max. 10mb)</small>
                  <i>
                    <Link to="/" className="text-decoration-none">
                      <small>*Download template</small>
                    </Link>
                  </i>
                </div>
              </FormGroup>
            </Col>
            <Col sm="12" lg="6">
              <FormGroup>
                <Label htmlFor="file2">Formulir Isian Kualifikasi</Label>
                <Input type="file" id="file2" name="file2" />
                <div className="d-flex justify-content-between">
                  <small className="text-muted">Upload file (max. 10mb)</small>
                  <i>
                    <Link to="/" className="text-decoration-none">
                      <small>*Download template</small>
                    </Link>
                  </i>
                </div>
              </FormGroup>
            </Col>
          </Row>
          <Row>
            <Col sm="12" lg="6">
              <FormGroup>
                <Label htmlFor="file3">Pakta Integritas</Label>
                <Input type="file" id="file3" name="file3" />
                <div className="d-flex justify-content-between">
                  <small className="text-muted">Upload file (max. 10mb)</small>
                  <i>
                    <Link to="/" className="text-decoration-none">
                      <small>*Download template</small>
                    </Link>
                  </i>
                </div>
              </FormGroup>
            </Col>
            <Col sm="12" lg="6">
              <FormGroup>
                <Label htmlFor="file4">HSE Plan</Label>
                <Input type="file" id="file4" name="file4" />
                <div className="d-flex justify-content-between">
                  <small className="text-muted">Upload file (max. 10mb)</small>
                </div>
              </FormGroup>
            </Col>
          </Row>
          <Row>
            <Col sm="12" lg="6">
              <FormGroup>
                <Label htmlFor="file5">List Man Power + CV</Label>
                <Input type="file" id="file5" name="file5" />
                <div className="d-flex justify-content-between">
                  <small className="text-muted">Upload file (max. 10mb)</small>
                </div>
              </FormGroup>
            </Col>
            <Col sm="12" lg="6">
              <FormGroup>
                <Label htmlFor="file6">Metode Pelaksanaan</Label>
                <Input type="file" id="file6" name="file6" />
                <div className="d-flex justify-content-between">
                  <small className="text-muted">Upload file (max. 10mb)</small>
                </div>
              </FormGroup>
            </Col>
          </Row>
          <Row>
            <Col sm="12" lg="6">
              <FormGroup>
                <Label htmlFor="file5">QA/QC Plan</Label>
                <Input type="file" id="file5" name="file5" />
                <div className="d-flex justify-content-between">
                  <small className="text-muted">Upload file (max. 10mb)</small>
                </div>
              </FormGroup>
            </Col>
          </Row>
          <hr />
          <FormGroup className="d-flex justify-content-end">
            <Button type="button" color="primary" className="me-2 d-flex gap-2">
              <MaterialIcon icon="send" />
              Kirim
            </Button>
          </FormGroup>
        </Form>
      </CardBody>
    </Card>
  );
};

export default UploadDocument;
